<template>
  <div class="info-container normal-container" >  
    <div class="info-div" style="height:100%"> 
      <div class="list-content  con-left" >
        <div class="list-header">
             <i class="hasIcon hasImg">
              <img src="dist/img/face/sh.png" />
            </i>
            下载列表
        </div>
        <div class="con_pe">
          <ul>
            <li v-for="(item, $index) in downloadList" @click.prevent="linkGo(item, $index)"  v-if="item.show" :class="{'rowselect': loadIndex === $index, '':loadIndex !== $index}">
                  {{item.text}}
               </li>
             </ul>
        </div>
      </div>
      <div  class="list-content con_center" style="overflow: hidden">
          <router-view ></router-view>
      </div>
    </div>
   </div>
</template>

<script>
   export default{
     data() { 
       return {
          controls: [],
          loadIndex: 0,
          currentTitle: ''
       }
     },
     computed: {
       downloadList: function () {
         return  [
           //  {text: '正向有功总报表下载', path: '/Main/DownloadReport/ActionTotal', title: '正向有功总报表下载', show: this.controls[32]},
           //  {text: '电能电费日结算报表下载', path: '/Main/DownloadReport/ElecClearingByDay', title: '电能电费日结算报表下载',show: this.controls[33]},
           //  {text: '电能电费月结算报表下载', path: '/Main/DownloadReport/ElecClearingByMon', title: '电能电费月结算报表下载',show: this.controls[34]},
             {text: '充值记录报表下载', path: '/Main/DownloadReport/Pay', title: '充值记录报表下载',show: this.controls[35] || true},
             {text: '退费记录报表下载', path: '/Main/DownloadReport/Refund', title: '退费记录报表下载',show: this.controls[36] || true}
          ]
       }
     },
     methods: {
       linkGo: function (item, index) {

          this.loadIndex = index;
          this.currentTitle = item.title;
          this.$router.push(item.path);
          localStorage.downloadLink  = JSON.stringify(item);
          this.$store.state.downloadLink = item;

       },
       backDownloadList: function () {
         this.$store.state.downloadLink = localStorage.downloadLink = '';
       },
       windowChange: function () {
          let that = this;
          this.resizeCon();
         /* window.onresize = function () {
               that.resizeCon();
          }*/
       },
       resizeCon: function () {
           let table = this.$refs.downloadPage;
           if(table && table.$refs.myTable) {
             table.getHeight();
           }
          
       },
       init: function () {
     
          for(let i = 0; i < this.downloadList.length; i++) {
            let item = this.downloadList[i];
            if(item.show) {
              this.loadIndex = i;
              this.$router.push(item.path);
              break;
            }
          }
          let that = this;
          /*window.onresize = function () {
              that.windowChange()
          }*/
       }
     },
     mounted: function () {
        this.isIn();
        this.getAction();
     },
    
   
   }
</script>

<style lang="scss">
.right-container .center-container {
 .download-container{
   margin-top: 2em;
   height: calc(100% - 1em);
   .all-container{
    //background: transparent;
  }
}
.normal-container {
 height: calc(100% - 1em);
 margin-top:1.1em;
 .showView-container{
  height:100%;
  width: 100%;
}
.con_center{
  .all-container{
    background: transparent!important;
    margin:0!important;
    box-shadow: none;
    width:100%;
    padding:0
  }
  .table-container{
    padding: 0;
    width:100%
  }
  .operate-div{
    margin-right:0;
    .date-picker{
      margin-right:0
    }
  }
}
}
}


</style>


